<template>
  <div>
    <div class="page startPage">
      <div class="logo" id="logo"></div>
      <div class="btn start-btn" @click="startGame"></div>
    </div>
    <div class="page restartPage" style="display: none">
      <div id="final-score">
        <div class="hint">Current Score</div>
        <div class="score"></div>
        <div
          class="wrapper"
          style="position: absolute; top: 0; text-align: center; width: 100%"
        >
        </div>
      </div>
      <div class="btn restart-btn" @click="restartGame"></div>
      <div class="highestscore">All-Time High Game Points: 500</div>
    </div>
    <div class="MyToast disappear"></div>
    <div class="maximumscore maximumscore-hide">
      <div>Your Highest GamePoints This Season</div>
      <div class="myscore">500</div>
    </div>
    <nav-menu
      on="game"
      :showValue="isShowNavMenu"
      @childClick="updateParentValue"
    ></nav-menu>
    <div class="nav blindBox">
      <ul>
        <li class="goblindbox">开盲盒</li>
      </ul>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { init } from "../game/index.js";
import Game from "../game/game.js";
import NavMenu from "@/components/NavMenu.vue";
import axios from "axios";
import CryptoJS from "crypto-js";
import { get, post } from "@/lib/API";

function parseInitData(initData) {
  return Object.fromEntries(new URLSearchParams(initData).entries());
}

function generateDataCheckString(data) {
  const sortedKeys = Object.keys(data)
    .filter((key) => key !== "hash")
    .sort();
  return sortedKeys.map((key) => `${key}=${data[key]}`).join("\n");
}

function generateSecretKey(botToken) {
  return CryptoJS.HmacSHA256(botToken, "WebAppData").toString(CryptoJS.enc.Hex);
}

function verifyData(initData, botToken) {
  const data = parseInitData(initData);
  const dataCheckString = generateDataCheckString(data);
  const secretKey = generateSecretKey(botToken);
  const calculatedHash = CryptoJS.HmacSHA256(
    dataCheckString,
    secretKey
  ).toString(CryptoJS.enc.Hex);
  console.log(dataCheckString, secretKey, calculatedHash, data.hash);
  return calculatedHash === data.hash;
}

export default {
  name: "Home",
  components: {
    NavMenu,
  },
  data() {
    return {
      initData: "query_id=AAEtYaFsAgAAAC1hoWxZEuhd&user=%7B%22id%22%3A6117482797%2C%22first_name%22%3A%22Okapi%20Gaming%22%2C%22last_name%22%3A%22%22%2C%22username%22%3A%22OkapiGaming%22%2C%22language_code%22%3A%22zh-hans%22%2C%22allows_write_to_pm%22%3Atrue%7D&auth_date=1718621191&hash=27dd0d00b8b26c0fa01d698e3f1b816248231de5a4b3cdb10a27ed04e9ca1da9" || '',
      initDataUnsafe: Telegram.WebApp.initDataUnsafe || '',
      isVerified: false,
      userAddress: localStorage.getItem("userAddress"),
      session_id: null

    };
  },
  setup() {
    const isShowNavMenu = ref(false);
    const showToast = (value) => {
      isShowNavMenu.value = value;
    };

    return {
      isShowNavMenu,
      showToast,
    };
  },
  mounted() {
    const getToken = localStorage.getItem("token");
    if (!getToken) {
      this.userLogin();
    }
    const isFirstVisit = window.localStorage.getItem("hasVisitedthree") || "";
    if (!isFirstVisit) {
      this.initializeGame();
    }
    this.getSeasons();
    if (this.initDataUnsafe && this.initDataUnsafe.user) {
      localStorage.setItem('userTgId', this.initDataUnsafe.user.id)
    }
    // this.getUserData();
    // 调用游戏初始化函数init();
    /*const game = new Game();
    window.$game = game;
*/
    init();
    console.log("******game", window, window.$game);

    this.bindEvents();

  },
  methods: {
    userLogin() {
      post("/tguser/login", { initData: this.initData })
        .then((response) => {
          if (response.code === 0) {
            console.log(response.data)
            localStorage.setItem('token', response.data.token)
            localStorage.setItem('userAddress', response.data.user.nickName)
            localStorage.setItem('phone', response.data.user.phone)
            localStorage.setItem('userName', response.data.user.userName)
            localStorage.setItem('user', JSON.stringify(response.data.user))
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async getSeasons() {
      const res = await get("/season/get_seasons");
      if (res.code === 0) {
        let data = res.data[0].name.split("~");
        let today = new Date();
        let year = today.getFullYear();
        let start_day = new Date(`${year}-${data[0]} 00:00:00`).getTime();
        let end_day = new Date(`${year}-${data[1]} 23:59:59`).getTime();
        let nowTime = today.getTime();
        return nowTime >= start_day && nowTime <= end_day;
      }
    },
    async startGame() {
      // let bol = await this.getSeasons() // 判断是否在赛季时间之内
      this.showToast(true);
      const startpage = document.querySelector(".startPage");
      const navMenu = document.querySelector(".navMenu");
      const maximumscore = document.querySelector(".maximumscore");

      startpage.style.display = "none";
      maximumscore.style.display = "flex";
      window.$game.start();
      // 开始游戏前调一个接口
      const res = await post("/season/start_game", { season_id: '1' })
      if (res.code === 0) this.session_id = res.data.session_id
    },
    async restartGame() {
      this.showToast(true);
      const restartpage = document.querySelector(".restartPage");
      const navMenu = document.querySelector(".navMenu");
      const maximumscore = document.querySelector(".maximumscore");

      restartpage.style.display = "none";
      maximumscore.style.display = "flex";

      window.$game.restart();
      // 开始游戏前调一个接口
      const res = await post("/season/start_game", { season_id: '1' })
      if (res.code === 0) this.session_id = res.data.session_id
    },
    login() {
      console.log("login");
    },
    bindEvents() {
      const game = window.$game;
      const restartpage = document.querySelector(".restartPage");
      const scoreEl = document.querySelector(".score");
      const blindBox = document.querySelector(".blindBox");
      const navMenu = document.querySelector(".navMenu");
      const maximumscore = document.querySelector(".maximumscore");

      game.failCallback = (score) => {
        this.showToast(false);
        restartpage.style.display = "flex";
        blindBox.style.display = "none";
        maximumscore.style.display = "none";
        scoreEl.innerHTML = score;
        // 游戏结束调一个接口 
        post("/season/end_game", {
          session_id: this.session_id,
          score: score,
          sign: 'string',
          timestamp:  Math.floor(new Date().getTime() / 1000)
        });
      };
    },
    initializeGame() {
      const routeUrl = process.env.ROUTE_URL || "../";

      // 初始化游戏逻辑
      const files = [
        "cd.mtl",
        "cd.obj",
        "cheng.mtl",
        "cheng.obj",
        "d_hezi4.mtl",
        "d_hezi4.obj",
        "d_hezi5.mtl",
        "d_hezi5.obj",
        "d_pixiang.mtl",
        "d_pixiang.obj",
        "d_tiaotai.mtl",
        "d_tiaotai.obj",
        "d_xiaoniao.mtl",
        "d_xiaoniao.obj",
        "d_yuanzhu1.mtl",
        "d_yuanzhu1.obj",
        "dangao.mtl",
        "dangao.obj",
        "huaban.mtl",
        "huaban.obj",
        "pingtai1.mtl",
        "pingtai1.obj",
        "q_hezi4.mtl",
        "q_hezi4.obj",
        "q_shudun.obj",
        "q_shudun.mtl",
        "shoutixiang.obj",
        "shoutixiang.mtl",
        "shudun.mtl",
        "shudun.obj",
        "x_fangzhuo.mtl",
        "x_fangzhuo.obj",
        "x_hezi5.mtl",
        "x_hezi5.obj",
        "x_huaban.mtl",
        "x_huaban.obj",
        "x_xigua.mtl",
        "x_xigua.obj",
        "x_yuanzhu1.mtl",
        "x_yuanzhu1.obj",
      ];
      files.forEach((file) => {
        fetch(routeUrl + "assets/obj/" + file)
          .then((response) => {
            if (response.ok) {
              return response.text();
            } else {
              throw new Error(`Failed to load file: ${file}`);
            }
          })
          .then((data) => {
            // 处理加载的文件数据
            // 在这里可以执行适当的操作，如渲染3D模型等
          })
          .catch((error) => {
            console.error(error);
          });
      });

      window.localStorage.setItem("hasVisitedthree", "true");
    },
    updateParentValue(value) {
      this.isShowNavMenu = value;
      this.showToast(value);
      console.log("updateParentValue", value);
    },
  },
};
</script>

<style lang="scss">
@import url("https://apps.bdimg.com/libs/fontawesome/4.2.0/css/font-awesome.min.css");
@import "@/assets/style.scss";

.maximumscore {
  position: fixed;
  top: 3rem;
  right: 3rem;
  width: 15rem;
  height: 3rem;
  font-size: 1.3rem;
  color: #333;
  z-index: 999;
  display: flex;
  .myscore {
    padding: 1rem;
    height: 4rem;
    border: 1px solid #333;
  }
}
.maximumscore-hide {
  display: none;
}
.highestscore {
  font-size: .8rem;
  margin-top: 1rem;
}
.logo{
  width: 100vw;
  height: 30vh;
  background: url('@/assets/img/icons/tapnhop.png') no-repeat center center/contain;
}
.btn {
    width: 17rem;height: 7rem;
    background: url('@/assets/img/icons/start-game.png') no-repeat center center/contain;
  }
  .restart-btn{
    background-image: url('@/assets/img/icons/try-again.png');
  }
  
  #final-score {
    width: 100vw;
    height: 28rem;
    background: url('@/assets/img/icons/score_bg.png') no-repeat center center/83%,
    url('@/assets/img/icons/light.png') no-repeat center center/108%;
    .hint {
      margin-top: 8.8rem;
      color: #0C3DED;
      font-weight: bold;
      font-size: 1.1rem;
    }
    .score{
      font-size: 6rem;
