        this.seasonDate = res.data[0].name
      }
      console.log(res, 999);
    },
    async getMySign() {
        const that = this;
      try {
        const getUseraddress = await this.initAccounts();
        if (getUseraddress) {
          post('/distribution/claimReward', { seasonId: 1 , userAddress: getUseraddress})
            .then(response => {
              if (response.code === 0) {
                const { seasonId, reward_list, unique_id, sign } = response.data;
                if (reward_list && reward_list.length > 0) {
                  const getRewardList = that.rewardList(reward_list);
