      const { address, chainId, isConnected } = useWeb3ModalAccount()
      if (!address.value) {
        const getModal = useWeb3Modal()
        getModal.open()
      }
      if (isConnected && address.value) {
        return address.value
      }
    },
    async userClaimTransaction(season, receiverInfos, userAddress, uniqueId, sign) {  
      console.log('userClaimTransaction', season, receiverInfos, userAddress, uniqueId, sign)
      const web3 = new Web3(window.ethereum);
      console.log('web3', web3)
      const contract = new web3.eth.Contract(RewardDistributorAbi, payuContractAddress);
      console.log(contract)
      try {
        await contract.methods.distribute( season, receiverInfos, userAddress, uniqueId, sign)
          .send({ from: userAddress, gas: 500000 })
          .on('receipt', (receipt) => {
              console.log("Transaction receipt:", receipt);
          });
        } catch (error) {
          console.error("Error calling distribute:", error);
      }
      
		},
    async gosendTransaction(seasonId, getRewardList, getUseraddress, unique_id, sign) {
      const web3 = new Web3(window.ethereum);
      const contract = new web3.eth.Contract(RewardDistributorAbi, payuContractAddress);
			const gasPrice = await web3.eth.getGasPrice();
      try {  
				const transaction = {  
					gasPrice: gasPrice,
					gas: "500000",
					from: getUseraddress,
					to: payuContractAddress,
					data: contract.methods.distribute( seasonId, getRewardList, getUseraddress, unique_id, sign).encodeABI(), // Contract functions and parameters  
				};  
        console.log(transaction)
				web3.eth.sendTransaction(transaction).on('transactionHash', function(hash){
						console.log(hash)
				})
				.on('receipt', function(receipt){
					console.log(receipt)
				})
				.on('confirmation', function(confirmationNumber, receipt){ 
					console.log(confirmationNumber, receipt)
				 })
				.on('error', function(receipt){
					console.log(receipt)
				}); // If a out of gas error, the second parameter is the receipt.
			} catch (error) {  
				console.error('Error:', error);
			}  
