const API_DOMAIN = process.env.API_DOMAIN || 'http://10.8.1.111:8888';

export function post(endpoint, data) {
  const token = localStorage.getItem('token');
  const formData = Object.entries(data).map(
    ([key, value]) =>
      value !== '' ? { [key]: value } : ''
  ).filter(Boolean);
  let newObj = {}
  formData.forEach((item) => {
    newObj = { ...newObj, ...item }
  })
  return fetch(`${API_DOMAIN}${endpoint}`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      'x-token': token ?? '',
    },
    body: JSON.stringify(newObj),
  }).then(res =>  {
    if (res.status === 200) {
      return res.json()
    }
    if (res.status === 500) {
      return res.json().then(result => {  
        if (result.code === 500){
          switch( result.msg ) {  
            case "Invalid Token":  
              // login timeout
              window.localStorage.clear();
              window.location.reload();
              break;  
            case "MatamaskAlreadyBoundError":  
              return {
                code: result.code,
                msg: 'This wallet address is already in use, please select another one.'
              }
            case "EmailAlreadyBoundError":  
              return {
                code: result.code,
                msg: 'The email is already in use, please select another one.'
              }
            case "SteamAlreadyBoundError":  
              return {
                code: result.code,
                msg: 'The Steam account is already bound, please select another account.'
              }
            default:  
              return {
                code: result.code,
                msg: result.msg
              }
          }
        }
      })
    }
  });
}

export function get(url, data = {}) {
  try {
    let query = '';
    const token = localStorage.getItem('token');
    Object.entries(data).forEach(([key, value], index) => {
      if (index === 0) {
        query = '?' + `${key}=${value}`;
      } else {
        query += '&' + `${key}=${value}`;
      }
    });
    return fetch(`${API_DOMAIN}${url}${query}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        // 'Access-Control-Allow-Origin': '*',
        'x-token': token ?? '',
      }
    }).then(response => {
      return response.json();
    }).then(res => {
      if (res.code === 0) {
        return res;
      }
      return {
        code: res.code,
        msg: res.msg
      }
    }).catch(error => {
      return {
        code: 400,
        msg: 'Request failure!'
      }
    });
  } catch (error) {
    return {
      code: 400,
      msg: 'Request failure!'
    }
  }
}